button {
    background-color: rgb(16, 16, 88);
    border-color: rgb(16, 16, 88);
    
}

#bttn {
    color: white;
    background-color: rgb(16, 16, 88);
    border-color: rgb(16, 16, 88);
    -webkit-animation: wiggle 200ms 5s 16 forwards;
            animation: wiggle 200ms 5s 16 forwards;
}

.carouselDiv {
    box-shadow: 10px 5px 5px rgb(61, 60, 78);
};

@-webkit-keyframes wiggle {
    from {
        -webkit-transform: rotateZ(-10deg);
                transform: rotateZ(-10deg);
    }
    to {
        -webkit-transform: rotateZ(10deg);
                transform: rotateZ(10deg);
    }
}

@keyframes wiggle {
    from {
        -webkit-transform: rotateZ(-10deg);
                transform: rotateZ(-10deg);
    }
    to {
        -webkit-transform: rotateZ(10deg);
                transform: rotateZ(10deg);
    }
}
